body {
  text-align: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

a:link, a:visited {
  color: #fff;
}

a:hover {
  color: #ff69b4;
}

a:active {
  transform: translateY(2px);
}

button, p {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  height: 48px;
  text-align: left;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow, transform;
  background-image: linear-gradient(90deg, #0f2 0, #fbff00 100%);
  border: 4px solid #000;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  padding-left: 16px;
  padding-right: 16px;
  font-family: JetBrains Mono, monospace;
  font-size: 18px;
  line-height: 1;
  text-decoration: none;
  list-style: none;
  transition: box-shadow .15s, transform .15s;
  display: inline-flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 4px rgba(45, 35, 66, .4), 0 7px 13px -3px rgba(45, 35, 66, .3), inset 0 -3px rgba(58, 65, 111, .5);
}

button:focus {
  box-shadow: inset 0 0 0 1.5px #005a0b, 0 2px 4px rgba(45, 35, 66, .4), 0 7px 13px -3px rgba(45, 35, 66, .3), inset 0 -3px #005a0b;
}

button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(45, 35, 66, .4), 0 7px 13px -3px rgba(45, 35, 66, .3), inset 0 -3px #005a0b;
}

button:active {
  transform: translateY(2px);
  box-shadow: inset 0 3px 7px #005a0b;
}

button:disabled {
  pointer-events: none;
  background-image: linear-gradient(90deg, #007d11 0, #7e8000 100%);
}

p {
  cursor: auto;
  display: flex;
}

